@font-face {
    font-family: 'Caveat';
    src: url('font/Caveat-VariableFont_wght.ttf');
}

:root {
    transition: font-size 0.5s ease;
}

:root.light {
    --bg-base: #ECE3D4;
    --bg-footer: #E3D6C3;
    --border: #C7A26B;
    --title-font: #372A15;
    --title-sub-font: #5C4523;
    --main-text-title-font: #111827;
    --main-text-font: #374151;
    --hover-brightness: 0.9
}

:root.dark {
    --bg-base: #333333;
    --bg-footer: #E3D6C3;
    --border: #C7A26B;
    --title-font: #372A15;
    --title-sub-font: #5C4523;
    --main-text-title-font: #111827;
    --main-text-font: #374151
}

body {
    background-color: var(--bg-base);
    color: var(--bg-color5);
}

.bg-transition {
    transition: background-color 0.3s ease, color 0.3s ease;
}

/*데스크탑*/
@media all and (min-width:1279px) {
    html {
        font-size: 18px;
        /* 기본 폰트 사이즈를 18px로 설정 */
    }
}

/*노트북 & 태블릿 가로 : 1024px-1279px*/
@media all and (min-width:1024px) and (max-width:1279px) {
    html {
        font-size: 16px;
        /* 기본 폰트 사이즈를 16px로 설정 */
    }
}

/*태블릿 가로 : 769px-1023px*/
@media all and (min-width:768px) and (max-width:1023px) {
    html {
        font-size: 15px;
        /* 기본 폰트 사이즈를 15px로 설정 */
    }
}

/*모바일 가로 & 태블릿 세로 : 480px-767px*/
@media all and (min-width:480px) and (max-width:767px) {
    html {
        font-size: 14px;
        /* 기본 폰트 사이즈를 14px로 설정 */
    }
}

/*모바일 : -479px*/
@media all and (max-width:479px) {
    html {
        font-size: 13px;
        /* 기본 폰트 사이즈를 13px로 설정 */
    }
}

/* 가로 */
@media all and (orientation: landscape) {}

/* 세로 */
@media all and (orientation: portrait) {}

header {
    display: flex;
    border-bottom: 0.13rem solid var(--border);
    padding: 0.5rem 0rem;
    font-size: 2rem;
    font-family: 'Caveat';
    font-weight: 900;
}
footer {
    background-color: var(--bg-footer);
    font-size: 0.8rem;
    padding: 0.8rem 0;
    text-align: center;
}

.sidebar-open {
    width: 5rem;
}

.sidebar-close {
    display: none;
}

.sidebar-button {
    margin: 0rem 1rem;
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 0.4rem;
    border: solid 0.18rem var(--border);
}


.sidebar-icon {
    width: 100%;
    height: 0.35rem;
    transition: all 0.3s ease;
    background-color: var(--bg-footer);
}
.sidebar-icon-outer{
    width: 50%;
    overflow: visible;
}
.icon-m {
    margin: 0.2rem 0;
}
.sidebar-button:hover
.icon-m{
    opacity: 0;
    transform: scale(0.5);
}
.sidebar-button:hover
.icon-t-close{
    transform: translate(-0.25rem, 0.2rem) rotate(45deg);
}
.sidebar-button:hover
.icon-t-open{
    transform: translate(-0.25rem, 0.2rem) rotate(-45deg);
}
.sidebar-button:hover
.icon-b-close{
    transform: translate(-0.25rem, -0.2rem) rotate(-45deg);
}
.sidebar-button:hover
.icon-b-open{
    transform: translate(-0.25rem, -0.2rem) rotate(45deg);
}
.sidebar-button:hover
.icon-arw {
    width: 1.2rem;
    height: 0.3rem;
}

.flex {
    display: flex;
}

.hober-able:hover {
    filter: brightness(var(--hover-brightness))
}

.click-able {
    cursor: pointer;
}
.w100{
    width: 100%;
}
.w50{
    width: 50%;
}
.visable {
    overflow: visible;
}